import { createStore as reduxCreateStore } from "redux"

const reducer = (state, action) => {
  if (action.type === `SETSTATE`) {
    return Object.assign({}, state, {
      count: action.count
    })
  }
  if (action.type === `SETUSERINFO`){
      return Object.assign({},state,{
        userInfo: action.userinfo
      })
  }
  if (action.type === `SETLV`){
      return Object.assign({},state,{
        lv: action.lv
      })
  }
  if (action.type === `SETLVS`){
      return Object.assign({},state,{
          lvs: action.lvs
      })
  }
  if (action.type === `SETTOTAL`){
      return Object.assign({},state,{
          total: action.total
      })
  }
  if (action.type === `SETTOTALBONUS`){
      return Object.assign({},state,{
          totalBonus: action.totalBonus
      })
  }
  if (action.type === `SETRANK`){
      return Object.assign({},state,{
          rank: action.rank
      })
  }
  if (action.type === `SETSHOW`){
      return Object.assign({},state,{
          showNav: action.show
      })
  }
  return state
}

const initialState = { count: null,userInfo: false,lv: null,lvs: null,total: null,showNav: false }

const createStore = () => reduxCreateStore(reducer, initialState)
export default createStore